<template>
  <div>工具</div>
</template>

<script>
export default {
  created() {
    document.addEventListener("keydown", this.eventF);
  },
  data() {
    return {};
  },
  methods: {
    eventF(e) {
      if ("ArrowLeft" === e.code) {
        this.$router.push("/s");
      } else if ("ArrowRight" === e.code) {
        this.$router.push("/n");
      }
    },
  },
};
</script>